.elements {
  margin: 0 auto 65px;
  max-width: 880px;
  display: flex;
  flex-wrap: wrap;
  gap: 20px 17px;
}

@media (max-width: 900px){
  .elements {
    max-width: 585px;
  }
}

@media (max-width: 600px){
  .elements {
    max-width: 285px;
    margin: 0 auto 48px;
    gap: 18px;
  }
}
